import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { ArtistAlbumComponent } from './artist-album/artist-album.component';
import { ArtistTrackComponent } from './artist-track/artist-track.component';
import { ArtistComponent } from './artist/artist.component';
import { HomeComponent } from './home/home.component';
import { ProfileComponent } from './profile/profile.component';
import { SearchComponent } from './search/search.component';


const routes: Routes = [
    {path: 'home', component: HomeComponent},
    {path: '', redirectTo: 'home', pathMatch: 'full'},
    {path: 'search', component: SearchComponent},
    {path: 'profile', component: ProfileComponent},
    {path: 'home2', component: HomeComponent},  // display home page content on home2 url
    {path: 'artist/:artistId', component: ArtistComponent,
        children: [
        {path: '', redirectTo: 'tracks', pathMatch: 'full'},
        {path: 'tracks', component: ArtistTrackComponent},
        {path: 'albums', component: ArtistAlbumComponent}
        ]
    },
    {path: '**', redirectTo: 'home'},  // redirect any unknow url to home page

];

@NgModule({
  imports: [RouterModule.forRoot(routes, {useHash: true})],  // change to HashLocationStrategy
  exports: [RouterModule]
})
export class AppRoutingModule { }
