import { Injectable } from '@angular/core';
import { HttpClient, HttpParams } from '@angular/common/http';
import { retry, map, tap, timeout, catchError } from 'rxjs/operators';
import { environment } from 'src/environments/environment';
import { Observable } from 'rxjs';

class SearchItem {
    constructor(public name: string,
                public artist: string,
                public link: string,
                public thumbnail: string,
                public artistId: string) {
    }
}

class ItuneDataItem {
    constructor(public trackName: string,
                public artistName: string,
                public trackViewUrl: string,
                public artworkUrl30: string,
                public artistId: string) {
    }
}

@Injectable({
    providedIn: 'root',
})

export class SearchService {
    apiRoot = 'https://itunes.apple.com/search';
    results: SearchItem[];

    constructor(private http: HttpClient,
    ) {
        this.results = [];
        if (environment.localApi) {
            this.apiRoot = 'assets/apple_music.json';
            // this.apiUrl = "assets/apple_music.wrong.api.json";
        } else {
            this.apiRoot = 'https://itunes.apple.com/search';
        }
    }

    private handleError(type: string, level: string): void {
        console.error(type);
    }

    search(term: string) {
        return new Promise((resolve, reject) => {
            this.results = [];
            const apiURL =
                `${this.apiRoot}?term=${term}&media=music&limit=20&callback=JSONP_CALLBACK`;

            if (environment.localApi) {
                 return this.callLocal2(this.http.get(apiURL), resolve, reject);
            } else {
                // use jsonp to avoid CORS issue
                this.callLocal2(this.http.jsonp(apiURL, 'callback'), resolve, reject);
            }
        });
    }

    private callLocal2(ob: Observable<any>, resolve, reject): void {
        ob.toPromise().then(
            (res: any) => {
                // Success
                this.results = res.results.map(item => {
                    return new SearchItem(
                        item.trackName,
                        item.artistName,
                        item.trackViewUrl,
                        item.artworkUrl30,
                        item.artistId
                    );
                });
                resolve();
            },
            error => { // Error
                reject(error.status + ': ' + error.statusText);
            }
        );
    }

    private callLocal(apiURL) {

        // return this.http.get(apiURL).pipe(
        //     map((res: { id: number, text: string }[]) => {
        //         return res.map(item => {
        //             return new MyUser(item.id, item.text);
        //         });
        //     }));

        const search = new HttpParams().set('foo', 'moo').set('limit', '25');
        this.http.get<any>(apiURL, { params: search }).subscribe(res => {
            console.log(res);
            this.handleResult(res.results);

        });

    }

    handleResult(res: ItuneDataItem[]): void {
        const tempList: Array<SearchItem> = [];
        res.forEach(item => {
            const one = new SearchItem(
                item.trackName,
                item.artistName,
                item.trackViewUrl,
                item.artworkUrl30,
                item.artistId);
            tempList.push(one);
        });
        this.results = tempList;
    }


}
